.sendForm {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
}

.textarea {
    width: 976px;
    height: 40px !important;

    overflow: hidden;

    border-width: 0;
    padding: 12px 24px;

    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.6);
    opacity: 0.9;
}

.button {
    width: 120px;
    height: 40px;

    border-width: 0;

    background: #0066CC;
    border-radius: 20px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;

    color: #FFFFFF;
}

.button:hover {
    background: #0066CC;
    opacity: 0.5;
    border-radius: 20px;

    color: #002E5C;
}
.button:active {
    background: #CC1439;
    border-radius: 20px;

    color: #FFFFFF;
}
